    color: #444;
    line-height: 1.3;
    padding: .4em 1.4em .3em .8em;
    width: 250px;
    max-width: 100%; 
    box-sizing: border-box;
   margin: 20px auto;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0,0,0,.03);
    border-radius: 12px;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: "bi bi-chevron-compact-down";
